/**=====================
     typography css start
==========================**/
body {
  font-family: Vazirmatn, Vazirmatn, "Outfit", sans-serif;
  font-size: 14px;
  background-color: var(--body-color);
  color: var(--body-font-color);
  letter-spacing: 0.3px;
}

h1, .h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  margin: 0;
}

h1, .h1 {
  font-size: 30px;
  font-weight: 500;
  letter-spacing: 0.6px;
}
@media (max-width: 575.98px) {
  h1, .h1 {
    font-size: 28px;
  }
}

h2, .h2 {
  font-size: 26px;
  font-weight: 500;
  letter-spacing: 0.6px;
}
@media (max-width: 575.98px) {
  h2, .h2 {
    font-size: 24px;
  }
}

h3, .h3 {
  font-size: 22px;
  font-weight: 400;
  letter-spacing: 0.6px;
}
@media (max-width: 575.98px) {
  h3, .h3 {
    font-size: 20px;
  }
}

h4, .h4 {
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.6px;
}
[data-theme=dark] h4, [data-theme=dark] .h4 {
  color: var(--light-font);
}
@media (max-width: 575.98px) {
  h4, .h4 {
    font-size: 18px;
  }
}
@media (max-width: 991.98px) {
  h4, .h4 {
    font-size: calc(17px + 5 * (100vw - 320px) / 880);
  }
}

h5, .h5 {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.4;
  letter-spacing: 0.6px;
}
@media (max-width: 575.98px) {
  h5, .h5 {
    font-size: 14px;
  }
}
[data-theme=dark] h5, [data-theme=dark] .h5 {
  color: var(--light-font);
}

h6, .h6 {
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.6px;
}
[data-theme=dark] h6, [data-theme=dark] .h6 {
  color: var(--light-font);
}

span {
  font-weight: 400;
}
[data-theme=dark] span {
  color: var(--light-font);
}

span {
  display: inline-block;
}

p {
  font-size: 14px;
  margin-bottom: 0;
  letter-spacing: 0.3px;
}
[data-theme=dark] p {
  color: var(--light-font);
}

hr {
  border: var(--light-border);
  opacity: inherit;
}

.default-border {
  border: var(--light-border) !important;
}

.solid-border {
  border: 1px solid var(--solid-border);
}

a {
  font-size: 14px;
  text-decoration: none;
  color: var(--body-font-color);
  transition: 0.5s all;
}
a:hover {
  color: var(--theme-default);
  transition: 0.5s all;
  text-decoration: none;
}
a:focus-visible {
  outline: none;
}

pre {
  background-color: rgba(67, 185, 178, 0.1);
  padding: 20px 12px;
  border: var(--light-border);
}

blockquote {
  padding: 14px;
}
blockquote[class*=b-] {
  border-width: 4px !important;
}
blockquote .blockquote-footer {
  font-size: 14px;
}

.list-circle {
  list-style: circle;
}
[data-theme=dark] .list-circle li {
  color: var(--light-font);
}

.comment {
  color: var(--light-font);
  font-style: italic;
}

.tap-top {
  text-align: center;
  cursor: pointer;
  display: none;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 5;
  color: var(--white);
  background: var(--theme-default);
  border: none;
  padding: 7px;
  box-shadow: 0 0 10px #43B9B2;
  opacity: 0.6;
  transition: all 0.3s ease;
}
.tap-top:hover {
  transition: all 0.3s ease;
  opacity: 1;
}
.tap-top svg {
  width: 20px;
  height: 20px;
  stroke: var(--white);
}

.disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.my-gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 46px;
}
.my-gallery .grid-item {
  text-align: center;
  margin: 0;
}
.my-gallery figcaption {
  display: none;
}

.animation-style {
  display: flex;
  gap: unset;
  margin-bottom: 0 !important;
}
.animation-style .my-gallery .pswp-gallery {
  margin-bottom: 25px;
}
.animation-style .my-gallery .pswp-gallery .img-thumbnail {
  border: var(--light-border);
}
[data-theme=dark] .animation-style .my-gallery .pswp-gallery .img-thumbnail {
  border-color: var(--light-font);
}

.typography-table thead tr th {
  font-weight: 600;
}
@media (max-width: 767.98px) {
  .typography-table thead tr th {
    min-width: 105px;
  }
}
.typography-table tbody tr td:first-child {
  min-width: 85px;
}

.card-wrapper {
  padding: 14px;
}
.card-wrapper dl dt {
  font-weight: 500;
}

.clipboaard-container h6, .clipboaard-container .h6 {
  line-height: 1.6;
  font-size: 14px;
}

.clipboaard-container p {
  margin-bottom: 8px;
}

.listing .card-body .card-wrapper {
  border: var(--light-border);
  color: var(--body-font-color);
}
.listing .card-body .card-wrapper .sub-title {
  font-size: 16px;
  margin-bottom: 12px;
}
.listing .card-body .card-wrapper ul li {
  margin-bottom: 5px;
}
.listing .card-body .card-wrapper ul li:last-child {
