/**==loader variable==**/
/**==Calculated variables==**/
.loader-wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  background-color: #ffffff;
  z-index: 12;
  top: 0;
}
.loader-wrapper .loader {
  width: 100%;
  height: 100%;
  transform: rotate(165deg);
}
.loader-wrapper .loader:before, .loader-wrapper .loader:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 4px;
  transform: translate(-50%, -50%);
}
.loader-wrapper .loader:before {
  animation: before 2s infinite;
}
.loader-wrapper .loader:after {
  animation: after 2s infinite;
}

@keyframes before {
  0% {
    width: 8px;
    box-shadow: 16px -8px #43B9B2, -16px 8px #FD7E40;
  }
  35% {
    width: 40px;
    box-shadow: 0 -8px #43B9B2, 0 8px #FD7E40;
  }
  70% {
    width: 8px;
    box-shadow: -16px -8px #43B9B2, 16px 8px #FD7E40;
  }
  100% {
    box-shadow: 16px -8px #43B9B2, -16px 8px #FD7E40;
  }
}
@keyframes after {
  0% {
    height: 8px;
    box-shadow: 8px 16px #C280D2, -8px -16px #2E8DD3;
  }
  35% {
    height: 40px;
    box-shadow: 8px 0 #C280D2, -8px 0 #2E8DD3;
  }
  70% {
    height: 8px;
    box-shadow: 8px -16px #C280D2, -8px 16px #2E8DD3;
  }
  100% {
    box-shadow: 8px 16px #C280D2, -8px -16px #2E8DD3;
  }
}
