/**=====================
    starterkit css start
==========================**/
.card .card-header .card-header-right {
  right: 19px;
  top: 12px;
  display: inline-block;
  /* float: right; */
  padding: 8px 0;
  position: absolute;
  background-color: #ffffff;
  z-index: 1;
}
[data-theme=dark] .card .card-header .card-header-right {
  background-color: var(--card-color);
}
@media (max-width: 575.98px) {
  .card .card-header .card-header-right {
    top: 16px;
  }
}
.card .card-header .card-header-right .card-option {
  text-align: right;
  width: 35px;
  height: 20px;
  overflow: hidden;
  transition: 0.3s ease-in-out;
}
.card .card-header .card-header-right .card-option li {
  display: inline-block;
}
.card .card-header .card-header-right .card-option li:first-child i {
  transition: 1s;
}
.card .card-header .card-header-right .card-option li:first-child i.icofont {
  color: unset;
}
.card .card-header .card-header-right i {
  margin: 0 5px;
  cursor: pointer;
  line-height: 20px;
}
[data-theme=dark] .card .card-header .card-header-right i {
  color: var(--light-font);
}
.card .card-header .card-header-right i.icofont-refresh {
  font-size: 13px;
}

.starter-main .card-body p {
  font-size: 14px;
  margin-bottom: 16px;
}
.starter-main .card-body ul {
  padding-left: 30px;
  list-style-type: disc;
  margin-bottom: 15px;
}
.starter-main .card-body ul li {
  display: list-item;
}
.starter-main .card-body h4, .starter-main .card-body .h4 {
  font-size: 18px;
}
.starter-main .card-body pre {
  white-space: pre-line;
  padding: 30px;
}
.starter-main .card-body .alert-primary.inverse:before {
  top: 32px;
}
.starter-main .alert i {
  display: flex;
  align-items: center;
}

.starter-kit-fix .page-body {
  margin-bottom: 52px;
}

.starter-main .alert-primary.inverse {
  padding: 13px 20px 13px 65px;
  border-color: var(--theme-default);
}
[dir=rtl] .starter-main .alert-primary.inverse {
  padding: 13px 65px 13px 20px;
}
.starter-main .alert-primary.inverse:before {
  content: "";
  position: absolute;
  left: 54px;
  width: 0;
  height: 0;
  border-left: 7px solid var(--theme-default);
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
}
[dir=rtl] .starter-main .alert-primary.inverse:before {
  border-right: 7px solid var(--theme-default);
  border-left: unset;
}
.starter-main .alert-primary.inverse i {
  padding: 17px 20px;
  display: flex;
  align-items: center;
  background-color: var(--theme-default);
  color: #ffffff;
  border-radius: 3px 0 0 3px;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
}
[dir=rtl] .starter-main .alert-primary.inverse i {
  right: 0;
  left: unset;
  margin-right: unset;
  margin-left: 5px;
}

.card.card-load .card-loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 8;
  align-items: center;
  justify-content: center;
}
.card.card-load .card-loader i {
  margin: 0 auto;
  color: var(--theme-default);
  font-size: 20px;
}

/**=====================
    starterkit css end
==========================**/
.general-widget .dropdown.icon-dropdown {
