.setting-sidebar.open {
  right: 0;
  transition: all 0.3s ease;
}
.setting-sidebar h4, .setting-sidebar .h4 {
  font-weight: 500;
}
.setting-sidebar .customizer-header {
  padding: 10px 20px;
  background: var(--theme-default);
}
.setting-sidebar .customizer-header .theme-title {
  display: flex;
